
import axios from 'axios';
import { allChampions, allMaps, mapStats, championStats} from './api'

export const getChampions = (champions,setChampions) => {
    axios.get("https://random-paladins-api.herokuapp.com/api/champions")
        .then( data=> {
          setChampions(data.data.champions);
    })
};
export const getMaps = (champions) => async () => {
    const Maps = await axios.get(allMaps());
    champions= Maps.data.results;
};
export const getChampionStats = (champions) => async () => {
    const ChampStats = await axios.get(mapStats());
    champions= ChampStats.data.results;
};
export const getMapStats = (champions) =>  async () =>{
    const MapStats = await axios.get(championStats());
    champions= MapStats.data.results;
};