const excludeHandler =(type, checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)=> {
    switch (type) {
      case "ALL":
        if (checkedState.includes(true)){
          const updatedCheckedState = checkedState.map (() => false);
          const updatedDisabled = champions.map((currentValue)=> currentValue.id);
          setCheckedState(updatedCheckedState);
          setDisabledChampions(updatedDisabled);
          setRandom(-1);
          setRandom2(-1);
          setRandom3(-1);
          setRandom4(-1);
          setRandom5(-1);
        }
        else {
          const updatedCheckedState = checkedState.map (() => true);
          const updatedDisabled = [];
          setCheckedState(updatedCheckedState);
          setDisabledChampions(updatedDisabled);
        } 
        break;
      case "Front Line":
        {
            const updatedCheckedState = champions.map ((currentValue,index) => currentValue.role==="Front Line" ? false : checkedState[index]);
              for (let index = 0; index < champions.length; index++) {
                if (champions[index].role==="Front Line" && !disabledChampions.includes(champions[index].id)) disabledChampions.push(champions[index].id);
              }
            setCheckedState(updatedCheckedState);
          }
        break;
      case "Damage":
        {
          const updatedCheckedState = champions.map ((currentValue,index) => currentValue.role==="Damage" ? false : checkedState[index]);
            for (let index = 0; index < champions.length; index++) {
              if (champions[index].role==="Damage" && !disabledChampions.includes(champions[index].id)) disabledChampions.push(champions[index].id);
            }
          setCheckedState(updatedCheckedState);
        }
        break;
      case "Flank":
        {
          const updatedCheckedState = champions.map ((currentValue,index) => currentValue.role==="Flank" ? false : checkedState[index]);
            for (let index = 0; index < champions.length; index++) {
              if (champions[index].role==="Flank" && !disabledChampions.includes(champions[index].id)) disabledChampions.push(champions[index].id);
            }
          setCheckedState(updatedCheckedState);
        }
        break;
      case "Support":
        {
          const updatedCheckedState = champions.map ((currentValue,index) => currentValue.role==="Support" ? false : checkedState[index]);
            for (let index = 0; index < champions.length; index++) {
              if (champions[index].role==="Support" && !disabledChampions.includes(champions[index].id)) disabledChampions.push(champions[index].id);
            }
          setCheckedState(updatedCheckedState);
        }
        break;
      default:
        break;
    }
}

export default excludeHandler;