import React, {useState,useEffect} from 'react';
import {getChampions, getMaps , getChampionStats ,getMapStats} from '../Brain/gettingData'
import { allChampions, allMaps, mapStats, championStats} from '../Brain/api'
import axios from 'axios';
import Champion from './Champion';
import RandomChampion from './RandomChampion';
import '../Styles/style.scss'
import excludeHandler from '../Brain/excludeHandler';
import Random from '../Brain/random';
import RandomTeam from '../Brain/randomteam';

const ChampionGen = () => {
  const [champions,setChampions]= useState([]);
  const [availableChampions,setAvailableChampions]= useState([]);
  const [checkedState,setCheckedState]= useState([]);
  const [random,setRandom]= useState(-1);
  const [random2,setRandom2]= useState(-1);
  const [random3,setRandom3]= useState(-1);
  const [random4,setRandom4]= useState(-1);
  const [random5,setRandom5]= useState(-1);
  const [disabledChampions, setDisabledChampions] = useState([]);
  useEffect(()=>{
    for (let index = 0; index < 49; index++) {
      checkedState.push(true);
    }
  },[]);
  useEffect(()=>{

  },[availableChampions]);
  const DisableOther=()=>{
    setRandom2(-1);
    setRandom3(-1);
    setRandom4(-1);
    setRandom5(-1);
  }
  return (
    <div className="championGenerator">
      <button onClick={()=>{Random(disabledChampions, setRandom, champions, availableChampions); DisableOther()}}>Random Champion</button>
      <button onClick={()=>RandomTeam(disabledChampions,setDisabledChampions, setRandom,setRandom2,setRandom3,setRandom4,setRandom5, champions, availableChampions,random, random2, random3, random4, random5)}>Random Team</button>
      <button onClick={()=>excludeHandler("ALL", checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)}>ALL</button>
      <button onClick={()=>excludeHandler("Front Line", checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)}>Disable Front Line</button>
      <button onClick={()=>excludeHandler("Damage", checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)}>Disable Damage</button>
      <button onClick={()=>excludeHandler("Flank", checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)}>Disable Flank</button>
      <button onClick={()=>excludeHandler("Support", checkedState, setCheckedState, setDisabledChampions, disabledChampions,champions,setRandom,setRandom2,setRandom3,setRandom4,setRandom5)}>Disable Support</button>
      <div className="grid">
        <RandomChampion random={random} availableChampions={availableChampions} setAvailableChampions={setAvailableChampions} champions={champions}/>
        <RandomChampion random={random2} availableChampions={availableChampions} setAvailableChampions={setAvailableChampions} champions={champions}/>
        <RandomChampion random={random3} availableChampions={availableChampions} setAvailableChampions={setAvailableChampions} champions={champions}/>
        <RandomChampion random={random4} availableChampions={availableChampions} setAvailableChampions={setAvailableChampions} champions={champions}/>
        <RandomChampion random={random5} availableChampions={availableChampions} setAvailableChampions={setAvailableChampions} champions={champions}/>
      </div>
      <Champion applyIcon={false} champions={champions} setChampions={setChampions} disabledChampions={disabledChampions} setDisabledChampions={setDisabledChampions} checkedState={checkedState} setCheckedState={setCheckedState}/>
    </div>
  );
}

export default ChampionGen;
