import React, { useEffect } from 'react';

const RandomChampion = ({availableChampions,setAvailableChampions, random,champions}) =>{
    return (
         <div className={`championCard random ${random ===-1 || availableChampions.length===0? 'vanish' : ''}`}>
                {random ===-1 || availableChampions.length===0 ? null : <img src={`${availableChampions[random].url}`} className="championImage"/>}
                {random ===-1 || availableChampions.length===0 ? null : <h2>{`${availableChampions[random].name}`}</h2>}
                {random ===-1 || availableChampions.length===0 ? null : <h3>{`${availableChampions[random].role}`}</h3>}     
        </div>
    );
}

export default RandomChampion;