import React, {useState,useEffect, useRef} from 'react';
import {getChampions} from '../Brain/gettingData'
import {TiTick, TiCancel} from 'react-icons/ti'

const Champion = ({applyIcon, champions, setChampions, disabledChampions,setDisabledChampions,setCheckedState,checkedState}) =>{
    const refer= useRef(null);
    useEffect (()=> {
        getChampions(champions,setChampions);
    }, []);
    const handleOnChange = (id) => {
        if (checkedState[id]){
            disabledChampions.push(id);
        }
        else {
            disabledChampions.splice(disabledChampions.indexOf(id),1);
        }
        const updatedCheckedState = checkedState.map ((item, index) => index == id ? !item : item);
        setCheckedState(updatedCheckedState);
    }
    return (
        <div className="grid">{champions && (
                champions.map(item =>(
                    <div key={item.id} className={`championCard`}>
                        <img src={`${item.url}`} className="championImage click" onClick={()=> handleOnChange(item.id)}/>
                        <input type="checkbox" checked={checkedState[item.id]} onChange={()=> handleOnChange(item.id)}/>
                        <h2>{`${item.name}`}</h2>
                        <h3>{`${item.role}`}</h3>
                    </div>
                ))
            )}
        </div>
    );
}

export default Champion;